<template>
	<div class="bomb" v-show="ifShow">
		<transition name="show">
			<div class="bomb-main" v-if="ifShow">
				<div class="content1" v-if="type=='alert'">
					<slot name='alertContent'>
						<span>{{text}}</span>	
					</slot>
				</div>
				<div class="content2" v-if="type=='confirm'">
					<slot name='confirmContent'>
						{{text}}	
					</slot>
				</div>
				<div class="buttons" v-if="type=='confirm'">
					<button class="yes-btn" @click="yes">确定</button>
					<button class="no-btn" @click="no">取消</button>
				</div>
				<img class="close" src="./close.png" height="11" width="11" 
				v-if="type=='alert'"
				@click="close"></img>
			</div>
		</transition>
	</div>
</template>
<script>
	export default{
		data:function(){
			return {
				type:'confirm',
				ifShow: false,
				text:""
			}
		},
		methods:{
			alert:function(text){
				this.type = "alert";
				this.text = text;
				this.ifShow = true;
			},
			confirm:function(text){
				this.type = "confirm";
				this.text = text;
				this.ifShow = true;
			},
			yes:function(){
				this.ifShow = false
				this.$emit('yesCloseBomb');
			},
			no:function(){
				this.ifShow = false
				this.$emit('noCloseBomb');
			},
			close:function(){
				this.ifShow = false
				this.$emit("normalCloseBomb")
			}
		}

	}
</script>
<style scoped lang="less">

	.bomb{
		position: fixed;
		top:0px;
		left: 0px;
		bottom: 0px;
		right: 0px;
		background-color: rgba(0,0,0,0.7);
		z-index: 8;
		.show-enter,.show-leave-active{
			opacity: 0;
		}
		.show-enter-active,.show-leave-active{
		}
		.bomb-main{
			display: block;
			position: absolute;
			top:40%;
			left: 50%;
			width:260px;
			height: 136px;
			background-color: #fff;
			margin-left: -130px;
			margin-top: -78px;
			border-radius: 4px;
			overflow: auto;
			transition:all 0.1s ease-out;
			.content1{
				height: 100%;
				width: 100%;
				text-align: center;
				font-size: 14px;
				color:#333;
				span{
					display: inline-block;
					margin-top: 60px;
				}
			}
			.content2{
				width: 100%;
				margin-top: 40px;
				text-align: center;
				font-size: 14px;
				color:#333;
			}

			.close{
				position: absolute;
				top:10px;
				right:10px;
				cursor:pointer;
			}
			.buttons{
				position: absolute;
				bottom: 21px;
				width:100%;
				text-align: center;
				button{
					display: inline-block;
					width:61px;
					height: 23px;
					background-color: #BFBFBF;
					border:none;
					color:#fff;
					border-radius: 4px;
					outline: none;
					cursor: pointer;
				}
				button:hover{
					background-color: #6DA683;
				}
				.no-btn{
					
				}
				.yes-btn{
					margin-right: 50px;
				}
			}
		}
	}
</style>


