<template>
  <div id="leftnavbar">
    <div class="title">节能中心欢饮您！</div>
    <ul class="list">
    	<router-link v-for="(item,index) in navList" :to="item.path">
    		<li :class="{active:navListMap[path]==index}">
    			<img :src="item.img"><br>
    			<span>{{item.txt}}</span>
    		</li>
    	</router-link>
    </ul>
  </div>
</template>

<script>
  export default {
  	data:function(){
  		return {
  			navList:[
  				{img:"../../../static/dynamic.png",txt:"动态管理",path:'/dynamic'},
  				{img:"../../../static/knowledge.png",txt:"节能知识管理",path:'/knowledge'},
  				{img:"../../../static/policy.png",txt:"政策支持管理",path:'/policy'},
  				{img:"../../../static/notice.png",txt:"通知管理",path:'/notice'}
  			],
        navListMap:{
          dynamic:0,
          knowledge:1,
          policy:2,
          notice:3,
        },
        path:'',
  		}
  	},
    watch:{
      '$route.path':function(){
        this.changePath();
      }
    },
    methods:{
      changePath:function(){
        this.path = this.$route.path.split("/")[1];
      }
    },
    created:function(){
      this.changePath();
    }
  }
</script>

<style scoped lang="less">
  #leftnavbar {
    transition:all 0.2s linear;
    .title{
      width: 100%;
      height: 50px;
      line-height: 50px;
      background-color: #6da583;
      font-size: 16px;
      color:#fff;
      text-align: center;
    }
    .list{
      li{
        padding-top: 20px;
        padding-bottom: 20px;
        text-align: center;
        // transition: all 0.2s linear;
        img{  
          width:30px;
        }
        span{
          font-size: 12px;
          color:#687C96;
        }
      }
      li:hover{
        span{
          color:#A4C3EA;
        }
        img{
            -webkit-filter: brightness(140%); 
            filter: brightness(140%);
        }
      }
      .active{
        background-color: #212a33;
        span{
          color:#A4C3EA;
        }
        img{
            -webkit-filter: brightness(140%); 
            filter: brightness(140%);
        }
      }
    }
  }
  @media screen and (max-width:1079px){
    #leftnavbar {
      .title{
        display: none;
      }
      .list{
        li{
          span{
            display: none;
          }
        }
      }
    }
  }
</style>


