<template>
	<div class="changestypebtn">
		<div class="selectedBox" v-show="state===0">
			<p class="value">{{optionsMap[value]}}</p>
			<div class="arrowbtn" @click="showOption"><img src="./greenarrow.png"></div>
		</div>
		<ul class="options" v-show="state===1"> 
			<li :class="{active:value==item}" @click="hideOption(item)" v-for="item in options">{{optionsMap[item]}}</li>
		</ul>
	</div>
</template>

<script>
import VueRouter from 'vue-router';

export default {
	data:function(){
		return {
			options:[1,2,3],
			state:0,
			optionsMap:["","生活类","常识类","办公类"]
		}
	},
	props:{
		value:{
			type: Number,
			default:function(){
				return 1;
			}
		}
	},
	computed:{

	},
	filters:{

	},
	methods:{
		showOption:function(){
			this.state = 1;
		},
		hideOption:function(value){
			// this.value = value;
			this.state = 0;
			console.log(value);
			this.$emit('select',value);
		}
	}
}
</script>

<style scoped lang='less'>
	.changestypebtn{
		position: relative;
		top:5px;
		border:2px solid #6da583;
		border-radius: 18px;
		background-color:#6da583;
		display: inline-block;
		transition:width 0.2s linear;

		.selectedBox{
			position: relative;
			.value{
				float: right;
				height: 16px;
				line-height: 16px;
				padding:0px 15px;
				margin-right: 13px;
				color:#fff;
				font-size: 12px;
			}
			.arrowbtn{
				position: absolute;
				top: 1px;
				right: 1px;
				height: 14px;
				border-top-left-radius: 12px;
				border-bottom-left-radius: 18px;
				border-top-right-radius: 18px;
				border-bottom-right-radius: 18px;
				line-height: 14px;
				padding: 0px 4px 0px 4px;
				background-color: #fff;
				cursor: pointer;
				img{
					position: relative;
					top:-1px;
					height: 9px;
					-webkit-filter: brightness(70%); 
           			filter: brightness(70%);
           			transform: rotate(180deg);
				}
			}
		}
		.options{
			width:198px;
			height: 16px;
			line-height: 14px;
			padding-left: 14px;
			padding-right: 14px;
			li{
				
				float: left;
				margin:2px 4px;
				padding-left: 10px;
				padding-right: 10px;
				color:#fff;
				background-color: #6da583;
				border-radius: 18px;
				transition: all 0.2s linear;
				cursor: pointer;
				font-size: 12px;
			}
			.active{
				color:#6da583;
				background-color: #fff;
			}
			li:hover{
				color:#6da583;
				background-color: #fff;
			}
		}
	}
</style>


