<template>
	<div class="policy">
		<m-v-list 
		:data='listData' 
		:dataCount='dataCount'
		@getData='getDataApi'
		@edit='edit'
		@deleteIt='deleteIt'
		ref='list'
		v-show='!isShowNothing'></m-v-list>
		<div class="nothing" v-show='isShowNothing'>
			<img src="./nothing.png" height="68" width="69"><br>
			<span>没有找到相应内容哦，换个关键词试试吧!</span>
		</div>
		<div class="header">
			<div class="type-title">
				<span class="title">后台管理</span> | <span class="value">政策支持管理</span>
			</div>
			<form class="search-box">
				<input type="text" name="" v-model="searchContent">
				<button @click="searchHandler"><img src="../../module/back-stage/images/search.png" height="20" width="20" ></button>
			</form>
			<button class="add" @click="add"><img src="../../module/back-stage/images/add.png">新增政策支持</button>
		</div>
		<m-v-edit ref='edit' editTitle='policy'></m-v-edit>
		<m-v-bomb ref='bomb'>
			<span slot='alertContent' style="color:#FEC05B;margin-top:60px;display: inline-block">哎呀，出问题啦，请稍后再试</span>
		</m-v-bomb>
	</div>
</template>
<script>
	import mVList from 'components/m-v-list/m-v-list.vue';
	import mVEdit from 'components/m-v-edit/m-v-edit.vue';
	import mVChangetypebtn from 'components/m-v-changetypebtn/m-v-changetypebtn.vue';
	import mVBomb from 'components/m-v-bomb/m-v-bomb.vue';
	export default {
		data:function(){
			return {
				type:3,
				listData:[],
				pageCount:NaN,
				dataCount:NaN,
				searchContent:"",
				isShowNothing:false,
			}
		},
		computed:{
			// isShowNothing:function(){
			// 	retrun this.listData.length===0 
			// }
		},
		watch:{
			"$route.path":function(){
				if(this.$route.path=='/policy'){
					console.log(this.$route.path)
					this.$refs.list.currentPage = 0;
					this.listData = [];
					this.getDataApi();
				}
			}
		},
		methods:{
			getDataApi:function(page,handler){
				var _this = this;
				// 去除搜索不到的框
				_this.isShowNothing = false;
				if(!page) page = 0;
				if(!handler) handler = this.addData;

				// $.post('http://121.42.203.85/energycenter/public/api/admin/showSimplePushes',{
	   //        	  type:'A2005',
			 //      headPage:page,
				//   pushtype:_this.type
		  //       },function(data,status){
		  //         console.log(data);
		  //         // location.href='./backStage.html';	
		  //       })


				var formData = new FormData();
				formData.append("type","A2005");
				formData.append("pushtype",this.type);
				formData.append("headpage",page*16);
				var config = {
					headers:{
						'Content-Type':'application/x-www-from-urlencoded'
					}
				}

				this.$ajax.post('http://121.42.203.85/energycenter/public/api/admin/showSimplePushes',formData,config)
				.then(function(response){
					if(response.data.re==0){
						handler(response.data,page);
						return;
					}
					
					if(response.data.re == 3){
						var x = confirm("请先登录");
						if(x){
							location.href = './login.html';
						}
						return;
					}

					_this.$refs.bomb.alert();
				})

				// console.log(this.$ajax);
				// this.$ajax.post('/energycenter/public/api/admin/showSimplePushes',{
				// 	type:'A2005',
				// 	headPage:page,
				// 	pushtype:this.type
				// })
				// .then(function(response){
				// 	// handler(response.data,page)
				// 	var t = setTimeout(function(){
				// 		handler(response.data,page)
				// 	},1000)
				// })
			},
			addData:function(data,page){
				this.$set(this.listData,page,data.msg.pushes)
				this.dataCount = data.msg.pushcount || 150;
			},
			searchHandler:function(){
				// 改变路由
				this.$router.replace("/policy/search/"+this.searchContent);
				var _this = this;
				_this.listData = [];
				

				var formData = new FormData();
				formData.append("type","A2004");
				formData.append("key",this.searchContent);
				var config = {
					headers:{
						'Content-Type':'application/x-www-from-urlencoded'
					}
				}

				this.$ajax.post('http://121.42.203.85/energycenter/public/api/admin/searchPush',formData,config)
				.then(function(response){
					var data = response.data.msg.pushes;
					if(response.data.re==0){
						// console.log(!response.data.msg);
						if(data.length===0 || !response.data.msg){
							_this.isShowNothing = true;
							return ;
						}

						_this.isShowNothing = false;
						var pageCount = parseInt(data.length%16) === 0 ? parseInt(data.length/16)-1 : parseInt(data.length/16);
						// console.log("data: "+pageCount)
						var i=0,j=0;
						_this.listData = [];
						_this.listData[0] = [];
						for(var x=0;x<data.length;x++){
							if(i>15){
								i=0;
								_this.$set(_this.listData,++j,[]);
							}
							// console.log("data: "+i+" "+j)
							_this.$set(_this.listData[j],i,data[x]);
							i++;
						}
						_this.$refs.list.currentPage = 0;
						_this.pageCount = pageCount;
						_this.dataCount = data.length;
						return;
					}
					
					if(response.data.re == 3){
						var x = confirm("请先登录");
						if(x){
							location.href = './login.html';
						}
						return;
					}

					_this.$refs.bomb.alert();

				})
			},
			edit:function(id){
				console.log("edit2");
				this.$refs.edit.show(id);
			},
			deleteIt:function(id,page,index){
				var x = confirm("你确定要删除此条记录吗？");
				if(x){
					var _this = this;
					var formData = new FormData();
					formData.append("type","A2003");
					formData.append("pushid",id);
					var config = {
						headers:{
							'Content-Type':'application/x-www-from-urlencoded'
						}
					}
					this.$ajax.post('http://121.42.203.85/energycenter/public/api/admin/deletePush',formData,config)	
					.then(function(response){
						if(response.data.re == 0){
							_this.listData[page].splice(index, 1);
							_this.dataCount--;
						}
					})
				}
			},
			renewData:function(){
				this.$refs.list.currentPage = 0;
				this.listData = [];
				this.getDataApi();
			},
			add:function(){
				this.$refs.edit.show();
			}
		},
		components:{
			mVList:mVList,
			mVEdit:mVEdit,
			mVChangetypebtn:mVChangetypebtn,
			mVBomb:mVBomb
			
		},
		created:function(){
			this.getDataApi();
		}
	}
</script>
<style scoped lang="less">
	.policy{
		.nothing{
			position: absolute;
			top:50%;
			left: 50%;
			width: 300px;
			margin-left: -150px;
			margin-top: -50px;
			text-align: center;
			img{

			}
			span{

			}
		}
		.header{
			position: absolute;
			top:50px;
			left: 0px;
			right: 0px;
			height: 62px;
			padding-left: 20px;
			padding-right: 20px;
			line-height: 50px;
			.type-title{
				float:left;
				height: 50px;
				line-height: 50px;
				.title{
					font-size: 14px;
					color: #878686;
					// margin-left: 
				}
				.value{
					font-size: 16px;
					color:#333;
					font-weight: 800;
				}
			}
			.changetypebtn-wrapper{
				float: left;
				margin-top: 15px;
				margin-left: 10px;
				line-height: 20px;
			}
			.add{
				float: right;
				width:120px;
				height: 20px;
				margin-right: 20px;
				margin-top: 15px;
				background-color: #6da583;
				border-radius: 20px;
				color:#fff;
				border:none;
				outline: none;
				font-size: 12px;
				cursor: pointer;
				img{
					position: relative;
					top:1px;
					width:12px;
					margin-right: 5px;
				}
			}
			.search-box{
				width:268px;
				height: 20px;
				margin-top: 15px;
				background-color: #fff;
				float: right;
				box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.2);
				input[type='text']{
					float: left;
					width:215px;
					height: 18px;
					padding-left: 15px;
					font-size: 12px;
					border:none;
					outline:none;
				}
				button{
					float: right;
					width: 33px;
					height: 100%;
					background-color: #6da583;
					border:none;
					outline:none;
					img{
						width:12px;
						height: 12px;
						position: relative;
						top:1px;
					}
				}
			}
		}
	}
</style>


