import Vue from 'vue'
import VueRouter from 'vue-router'
import axios from 'axios'
import App from './App'
import mBasePolicy from 'components/m-base-policy/m-base-policy.vue';
import mBaseKnowledge from 'components/m-base-knowledge/m-base-knowledge.vue';
import mBaseNotice from 'components/m-base-notice/m-base-notice.vue';
import mBaseDynamic from 'components/m-base-dynamic/m-base-dynamic.vue';

Vue.prototype.$ajax = axios;
Vue.use(VueRouter);
const routes = [
	{path:'/dynamic',component:mBaseDynamic},
	{path:'/dynamic/search/:key',component:mBaseDynamic},
	{path:'/dynamic/search',component:mBaseDynamic},
	{path:'/knowledge/search/:key',component:mBaseKnowledge},
	{path:'/knowledge/search',component:mBaseKnowledge},
	{path:'/notice/search/:key',component:mBaseNotice},
	{path:'/notice/search',component:mBaseNotice},
	{path:'/policy/search/:key',component:mBasePolicy},
	{path:'/policy/search',component:mBasePolicy},
	{path:'/knowledge',component:mBaseKnowledge},
	{path:'/notice',component:mBaseNotice},
	{path:'/policy',component:mBasePolicy},
	{path:'/',redirect:'/dynamic'},
]
const router = new VueRouter({
	routes
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router:router,
  template: '<App/>',
  components: { App }
})



