<template>
	<div id="mVEdit" v-if="isShow">
		<div class="edit-title">
			<span class="title">后台管理</span> | <span class="value">{{editTypeMap[editTitle]}}</span>
			<button class="back-btn" @click="returnFromEdit"><img src="./back.png" height="14" width="9">返回</button>
				<m-v-changetypebtn :value='mainInformation.knowledgetype' @select='chengeKnowledgetype' v-if="mainInformation.pushtype===2"></m-v-changetypebtn>
		</div>
		<div class="edit-main">
			<div class="main-title">
				<span class="title">标题</span>
				<input type="text" name="" v-model='mainInformation.title'>
			</div>
			<div class="upload-cover">
				<span class="title">上传封面</span>
				<div class="coverImg" v-show="mainInformation.coverurl && mainInformation.coverurl!=='undefined'">
					<img :src="coverSrc">
				</div>
				<div class="button-wrapper">
					<button><img src="./upload.png" height="15" width="15">本地上传</button>
					<input type="file" name="" @change='uploadImg($event)'>
				</div>
				<span class="note">*如不设置将自动设置为默认图片</span>
			</div>
			<div class="main-author">
				<span class="title">编者</span>
				<input type="text" name="" v-model='mainInformation.author'>
				<span class="note">0/20</span>
			</div>
			<div class="upload-pdf" v-if="editTitle=='knowledge'">
				<span class="title">上传pdf</span>
				<div class="button-wrapper">
					<button><img src="./upload.png" height="15" width="15">本地上传</button>
					<input type="file" name="" @change='uploadImg($event)'>
				</div>
			</div>
			<div class="main-content" id="mainContent" ref="mainContent">
				<div class="toolbar-box" ref='toolbar'>
					<div id="editor-toolbar"></div>
					<button class="fullscreen-btn" @click='fullscreenHandler' style="background:none;border:none;cursor:pointer">全屏</button>
				</div>
	            <div id="editor-text" ref='editorText'></div>
			</div>
		</div>
		<div class="edit-buttons">
			<button class="preview-btn margin-right" @click='previewHandler'>预览</button>
			<button class="save-btn" @click='saveAndDisplay'>保存并发布</button>
		</div>
		<div class="fullscreen" v-show="isFullScreen" ref='fullscreen'></div>
		<div class="preview" v-if="isPreview">	
			<div class="main">
				<button class="back-btn" @click="retrunFromPreview"><img src="./back.png" height="14" width="9">返回</button>
				<h1 class="title">{{mainInformation.title}}</h1>
				<div class="content" v-html="previewContent"></div>
			</div>
		</div>
		<div class="cropper-wrapper" v-if='isCropper'>
			<div class="main">
				<div class="cropper-box">
					<vue-cropper
					:img="option.img"
					ref="cropper"
					:outputSize="option.size"
					:outputType="option.outputType"
					:info="true"
					:full="option.full"
					:autoCrop="option.autoCrop"
					:autoCropWidth="option.autoCropWidth"
					:autoCropHeight="option.autoCropHeight"
					:fixedBox='option.fixedBox'
					:fixed="option.fixed"
					:fixedNumber="option.fixedNumber"
					>
					</vue-cropper>
				</div>
				<div class="right">
					<div class="show-preview">
						<img :src="modelSrc">
					</div>
					<button @click="previewCropper" class="previewBtn">预览</button>
					<div class="buttons">
						<button class="marginRight" @click='sureCropper'>确定</button>
						<button @click='closeCropper'>取消</button>
					</div>
				</div>
			</div>
		</div>
		<m-v-bomb ref='bomb1' @yesCloseBomb='returnBack' @noCloseBomb='function(){}'></m-v-bomb>
		<m-v-bomb ref='bomb2' @yesCloseBomb='display' @noCloseBomb='function(){}'></m-v-bomb>
		<m-v-bomb ref='bomb3'>
			<span slot="alertContent" style="display:inline-block;margin-top: 33px;">
				<img src="./publishSucceed.png" height="40" width="40"/><br>
				<span>发布成功</span>
			</span>
		</m-v-bomb>
		<m-v-bomb ref='bomb4'>
			<span slot="alertContent" style="display:inline-block;margin-top: 60px;color:#FEC05B">
				<span>哎呀，出问题啦，请稍后再试</span>
			</span>
		</m-v-bomb>
	</div>
</template>
<script>
	import E from 'wangeditor';
	import vueCropper from 'vue-cropper';
	import mVBomb from 'components/m-v-bomb/m-v-bomb.vue';
	import mVChangetypebtn from 'components/m-v-changetypebtn/m-v-changetypebtn.vue';
	export default{
		data:function(){
			return {
				editor:undefined,
				editTypeMap:{
					dynamic:"动态管理",
					knowledge:"节能知识管理",
					policy:"政策支持管理",
					notice:"通知管理",
				},
				pushtypemap:{
					dynamic:1,
					knowledge:2,
					policy:3,
					notice:4,
				},
				isFullScreen:false,
				isPreview:false,
				previewContent:undefined,
				isCropper:false,
				option: {
					img: '',
					size: 1,
					full: false,
					outputType: 'png',
					autoCropWidth: 400,
					autoCropHeight: 300,
					autoCrop: true,
					fixed: true,
					fixedNumber: [4, 3]
				},
				modelSrc:"",
				coverSrc:"",
				isShow:false,
				id:undefined,
				mainInformation:{
					id: undefined, 
		            title: undefined, 
		            content: undefined, 
		            create_time: undefined, 
		            update_time: undefined, 
		            coverurl: undefined, 
		            pushtype: undefined, 
		            author: undefined, 
		            knowledgetype: 1
				},
				//类型为add或者edit
				editType:'add',
				imgblob:undefined
			}
		},
		props:{
			editTitle:{
				type: String,
				default:function(){
					return 'dynamic'
				}
			}
		},
		computed:{
			authorLength:function(){

			}
		},
		components:{
			vueCropper:vueCropper,
			mVBomb:mVBomb,
			mVChangetypebtn
		},
		methods:{
			show:function(id){
				this.isShow = true;
				this.id = id;
				if(this.id==undefined){
					// 新建功能
					this.resetData();
					this.editType = 'add';
				}else {
					// 编辑功能
					this.resetData();
					this.getDataApi();
					this.editType = 'edit';
				}
			},
			resetData:function(){
				var _this = this;
				this.mainInformation.id = undefined;
				this.mainInformation.title = undefined;
				this.mainInformation.content = undefined;
				this.mainInformation.create_time = undefined;
				this.mainInformation.update_time = undefined;
				this.mainInformation.coverurl = undefined;
				this.mainInformation.pushtype = this.pushtypemap[this.$route.path.split("/")[1]];
				this.mainInformation.author = undefined;
				this.mainInformation.knowledgetype = 1;
				this.coverurl = "";
				this.imgblob = undefined;
				this.modelSrc="";
				this.coverSrc="";
			},
			getDataApi:function(){
				var _this = this;

				var formData = new FormData();
				formData.append("type","A2006");
				formData.append("pushid",this.id);
				var config = {
					headers:{
						'Content-Type':'application/x-www-from-urlencoded'
					}
				}

				this.$ajax.post('http://121.42.203.85/energycenter/public/api/admin/showDetailPush',formData,config)
				.then(function(response){
					if(response.data.re==0){
						// 初始化mainInformation 
						_this.mainInformation = response.data.msg.push;
						_this.coverSrc = _this.mainInformation.coverurl;
						_this.editor.txt.html(_this.mainInformation.content);
						console.log(_this.mainInformation);
						return;
					}
					
					if(response.data.re == 3){
						alert("请先登录");
						location.href = './login.html';
					}

					_this.$refs.bomb.alert();
				})
				// 用main初始化coverSrc和content
			},
			doFullScreen:function(){
				var editorText = this.$refs.editorText;
				var cover = this.$refs.fullscreen;
				var toolbarContaner = this.$refs.toolbar;
				// console.log(cover);

	            editorText.style.position = 'absolute';
	            editorText.style.top = '71px';
	            editorText.style.left = '20px';
	            editorText.style.right = '20px';
	            editorText.style.bottom = '20px';
	            editorText.style.height = '';

	            cover.appendChild(toolbarContaner)
	            cover.appendChild(editorText)
			},
			unDoFullScreen:function(){
				var editorText = this.$refs.editorText;
				var toolbarContaner = this.$refs.toolbar;
				var container = this.$refs.mainContent;

				container.appendChild(toolbarContaner)
	            container.appendChild(editorText)
	            editorText.style.position = '';
	            editorText.style.height = '391px';
	            editorText.style.top = '0';
	            editorText.style.left = '0';
	            editorText.style.right = '0';
	            editorText.style.bottom = '0';
			},
			fullscreenHandler:function(){
				if(this.isFullScreen){
					this.isFullScreen = false;
					this.unDoFullScreen();
				}else {
					this.isFullScreen = true;
					this.doFullScreen();
				}
			},
			saveAndDisplay:function(){
				this.mainInformation.content = this.editor.txt.html();
				this.$refs.bomb2.confirm('确认发布吗？');
			},
			display:function(){
				var _this = this;
				if(this.editType=="add"){
					var formData = new FormData();
					formData.append("coverurl",this.mainInformation.coverurl);
					formData.append("type","A2001");
					formData.append("pushtype",this.mainInformation.pushtype);
					formData.append("title",this.mainInformation.title);
					formData.append("content",this.mainInformation.content);
					formData.append("knowledgetype",this.mainInformation.knowledgetype);
					formData.append("author",this.mainInformation.author);
					console.log(this.mainInformation);
					var config = {
						headers:{
							'Content-Type':'application/x-www-from-urlencoded'
						}
					}
					this.$ajax.post('http://121.42.203.85/energycenter/public/api/admin/addPush',formData,config)
					.then(function(response){
						if(response.data.re==0){
							_this.$refs.bomb3.alert('发布成功');
							return;
						}
						
						if(response.data.re == 3){
							alert("请先登录");
							location.href = './login.html';
							return;
						}

						_this.$refs.bomb4.alert();
					})
				}else {
					var formData = new FormData();
					formData.append("type","A2002");
					formData.append("pushid",this.mainInformation.id);
					formData.append("title",this.mainInformation.title);
					formData.append("author",this.mainInformation.author);
					formData.append("coverurl",this.mainInformation.coverurl);
					formData.append("content",this.mainInformation.content);
					formData.append("knowledgetype",this.mainInformation.knowledgetype);
					var config = {
						headers:{
							'Content-Type':'application/x-www-from-urlencoded'
						}
					}
					// console.log(formData);
					this.$ajax.post('http://121.42.203.85/energycenter/public/api/admin/updatePush',formData,config)
					.then(function(response){
						if(response.data.re==0){
							_this.$refs.bomb3.alert('发布成功');
							return;
						}
						
						if(response.data.re == 3){
							alert("请先登录");
							location.href = './login.html';
							return;
						}

						_this.$refs.bomb4.alert();
					})
				}
			},
			retrunFromPreview:function(){
				this.isPreview = false;
			},
			previewHandler:function(){
				this.previewContent = this.editor.txt.html()

				this.isPreview = true;
			},
			returnFromEdit:function(){
				this.$refs.bomb1.confirm('您还在编辑状态，确认返回吗？');
			},
			returnBack:function(){
				this.isShow = false;
			},
			uploadImg (e) {
				//上传图片
				// this.option.img
				var file = e.target.files[0]
				if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
					alert('图片类型必须是.gif,jpeg,jpg,png,bmp中的一种')
					return false
				}
				var reader = new FileReader()
				reader.onload = (e) => {
					let data
					if (typeof e.target.result === 'object') {
						// 把Array Buffer转化为blob 如果是base64不需要
						data = window.URL.createObjectURL(new Blob([e.target.result]))
					} else {
						data = e.target.result
					}
					this.option.img = data
					this.isCropper = true;
				}
				// 转化为base64
				// reader.readAsDataURL(file)
				// 转化为blob
				reader.readAsArrayBuffer(file)
			},
			
			closeCropper:function(){
				this.isCropper = false;
				this.modelSrc = '';
			},
			sureCropper:function(){
				this.$refs.cropper.getCropBlob((data) => {
					this.imgblob = data;
					// console.log(this.imgblob);
					this.sendImgApi();
					var img = window.URL.createObjectURL(data)
					this.model = true
					this.coverSrc = img
				})

				this.isCropper = false;
				this.modelSrc = '';
			},
			sendImgApi:function(){
				var formData = new FormData();
				formData.append("photo",this.imgblob);
				this.$ajax.post("http://121.42.203.85/energycenter/public/api/admin/uploadImageForUrl",formData)
				.then((response)=>{
					this.mainInformation.coverurl = response.data.msg.coverurl;
					// console.log(this.mainInformation);
				})
			},
			previewCropper:function(){
				this.$refs.cropper.getCropData((data) => {
					this.model = true
					this.modelSrc = data
				})
			},
			finish (type) {
				// 输出
				// var test = window.open('about:blank')
				// test.document.body.innerHTML = '图片生成中..'
				if (type === 'blob') {
					this.$refs.cropper.getCropBlob((data) => {
						var img = window.URL.createObjectURL(data)
						this.model = true
						this.modelSrc = img
					})
				} else {
					this.$refs.cropper.getCropData((data) => {
						this.model = true
						this.modelSrc = data
					})
				}
			},
			chengeKnowledgetype:function(value){
				this.$set(this.mainInformation,'knowledgetype',value);
				console.log(value);
				// this.mainInformation.knowledgetype = value;
			}
		},
		created:function(){console.log("create");},
		updated:function(){
			if(document.getElementById('editor-toolbar')){
				console.log("xxx");
				this.editor = new E('#editor-toolbar', '#editor-text');
				this.editor.customConfig.zIndex = 7;
				this.editor.customConfig.uploadImgShowBase64 = true
		        this.editor.create();
			}
		}
	}
</script>
