<template>
	<div id="mVList">
		<div class="main" id="mainListWrapper" ref="mainListWrapper">
			<ul class="list" id="mainList" ref = "mainList"
			:style="{width:mainWidth}"
			v-if="data[currentPage]">
				<li v-for='(item,index) in data[currentPage]' v-if = '!ifLoading'>
					<p class="date">发表日期：2016.12.03 12:31</p>
					<p class="content">{{item.content | contentFilter}}</p>
					<div class="img">
						<img :src="item.coverurl | imgFilter" height="220" width="330" class="srcimg">
						<a :href="'./detail.html#/'+item.pushtype+'/'+item.id" target="_blank">
							<div class="cover">
								<img src="./left.png" height="14" width="14" class='left'>
								<span>查看文章</span>
								<img src="./right.png" height="14" width="14" class="right">
							</div>
						</a>
					</div>
					<p class="author">{{item.author}}</p>
					<div class="button">
						<button @click="edit(item.id)"><img src="./write.png" height="17" width="17">编辑</button>
						<button @click='deleteIt(item.id,index)'><img src="./delect.png" height="17" width="17">删除</button>
					</div>
				</li>
			</ul>
			<img src="./loading.gif" height="36" width="36" class="loading"
			v-if='ifLoading'>
		</div>
		<div class="pages">
			<div class="record">记录{{dataCount}}条</div>
			<div class="pagescount">页数{{currentPage+1}}/{{endPage+1}}页</div>
			<div class="buttons">
				<button class="button1" @click="tofirstPage"><img src="./firstPage.png" height="11" width="10"></button>
				<button class="button1" @click="tolastPage"><img src="./lastPage.png" height="11" width="10"></button>
				<button class="button1" @click="tonextPage"><img src="./nextPage.png" height="11" width="10"></button>
				<button class="button1" @click="toendPage"><img src="./endPage.png" height="11" width="10"></button>
				<span>跳转到</span>
				<input type="text" name="" v-model="toPage" @keydown.enter="toaimPage">
				<button class="button2" @click="toaimPage">GO</button>
			</div>
		</div>
		<m-v-bomb ref='bomb'></m-v-bomb>
		<div class="bigCover" v-if="ifLoading"></div>
	</div>
</template>
<script>
	import mVBomb from 'components/m-v-bomb/m-v-bomb.vue';
	export default{
		data:function(){
			return {
				currentPage:0,
				toPage:undefined,
				mainWidth:"",
			}
		},
		components:{
			mVBomb:mVBomb
		},
		filters:{
			contentFilter:function(value){
				// console.log(value.split(0,20));
				return value.length>20 ? value.substring(0, 25)+"..." : value;
			},
			imgFilter:function(value){
				if(value==='undefined'){
					console.log("un");
				}
				return value==='undefined' ? '../../../static/bcimg.png' : value;
			}
		},
		props:{
			data:{
				type: Array,
				// default: function(){
				// 	return [];
				// }
			},
			dataCount:{
				default:function(){
					return 0
				}
			}
		},
		computed:{
			endPage:function(){
				// console.log(parseInt(this.dataCount/16))
				return this.dataCount%16==0 ? parseInt(this.dataCount/16)-1 : parseInt(this.dataCount/16)
			},
			ifLoading:function(){
				return this.data[this.currentPage] == undefined ? true : false;
			}
		},
		watch:{
			"currentPage":function(){
				this.$refs.mainListWrapper.scrollTop = 0;
				if(!this.data[this.currentPage]) this.tellParents();
			}
		},
		methods:{
			changeWidth:function(){
				// console.log(document.getElementById("mainListWrapper").className);
				var width = this.$refs.mainListWrapper.offsetWidth-10;
				var count = parseInt(width/261);
				var innerWidth = count*261+2;

				this.mainWidth = innerWidth+"px";
			},
			tonextPage:function(){
				if(this.currentPage<this.endPage) {
					this.currentPage++
				}
			},
			tolastPage:function(){
				if(this.currentPage>0){
					this.currentPage--;
				}
			},
			tofirstPage:function(){
				this.currentPage = 0;
			},
			toendPage:function(){
				this.currentPage = this.endPage;
			},
			toaimPage:function(){
				if(this.toPage-1<=this.endPage) {
					this.currentPage = parseInt(this.toPage)-1;
				}
				else{
					this.$refs.bomb.alert("没有此页")
				}
				this.toPage = undefined;
			},
			tellParents:function(){
				//通知父组件
				this.$emit("getData",this.currentPage);
			},
			edit:function(id){
				this.$emit("edit",id);
			},
			deleteIt:function(id,index){
				this.$emit("deleteIt",id,this.currentPage,index);
			}
		},
		created:function(){
			
			window.onresize = (function(){
				this.changeWidth();
			}).bind(this)
		},
		mounted:function(){
			this.changeWidth();
		}
	}
</script>
<style scoped lang="less">
	#mVList{
		position: absolute;
		top:0px;
		bottom:0px;
		width:100%;
		padding-top: 112px;
		padding-bottom: 66px;
		overflow: hidden;
		button{
			cursor: pointer;
		}
		.main::-webkit-scrollbar {
		    width:6px;
		}
		.main::-webkit-scrollbar-button{
			display: none;
		}
		.main::-webkit-scrollbar-thumb{
			background-color: #6da583;
			border-radius:4px;
		}
		.main::-webkit-scrollbar-track{
	        background: none;
	    }
		.main{
			margin: auto 20px;
			height: 100%;
			overflow: auto;
			position: relative;
			ul{
				padding-top: 4px;
				margin:auto;
				li{
					position: relative;
					width:221px;
					height: 302px;
					padding-left: 10px;
					padding-right: 10px;
					background-color: #fff;
					display: inline-block;
					box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.1);
					margin-right: 20px;
					margin-bottom: 28px;
					.date{
						font-size: 12px;
						color:#BFBEBE;
						height: 32px;
						line-height: 32px;
						text-align: left;
					}
					.content{
						font-size: 14px;
						color:#333;
						padding-top: 7px;
						padding-bottom: 7px;
						line-height: 16px;
						height: 36px;
						text-align: left;
					}		
					.img{
						position: relative;
						width:221px;
						height: 148px;
						background:#f7f7f7;
						cursor: pointer;
						text-align: center;
						line-height: 148px;
						overflow: hidden;
						.srcimg{
							vertical-align: middle;
							 max-width: 100%;  
    						 max-height: 100%;  
						}
						.cover{
							position: absolute;
							top:0px;
							width:100%;
							height: 100%;
							left: 0px;
							background-color: #212a33;
							line-height: 148px;
							text-align: center;
							color:#fff;
							opacity: 0;
							transition:opacity 0.2s linear;
							.left{
								position: relative;
								top:10px;
								right: 4px;
							}
							.right{
								position: relative;

								bottom: 10px;
								left: 4px;
							}
						}
					}
					.img:hover .cover{
						opacity: 0.8;
					}
					.author{
						font-size: 12px;
						color:#BFBEBE;
						height: 32px;
						line-height: 32px;
						text-align: right;
					}
					.button{
						width:241px;
						height: 42px;
						background-color: #6da583;
						position: absolute;
						bottom: 0px;
						left: 0px;
						button{
							float: left;
							width:120px;
							height: 100%;
							background:none;
							border:none;
							outline: none;
							font-size: 14px;
							color:#fff;
							transition: all 0.1s linear;
							img{
								position: relative;
								top:2px;
								display: inline-block;
								margin-right: 8px;
							}
						}
						button:hover{
							background-color: #5B8A6D;
						}
					}
				}
			}
			.loading{
				position: absolute;
				top:50%;
				left: 50%;
				margin-top: -18px;
				margin-left: -18px;
			}
		}
		.pages{
			position: absolute;
			bottom: 0px;
			left: 0px;
			right: 0px;
			padding-left: 20px;
			padding-right: 20px;
			height: 66px;
			.record{
				float: left;
				height: 66px;
				line-height: 66px;
				font-size: 14px;
				color:#555555;
				margin-right: 60px;
			}
			.pagescount{
				float: left;
				height: 66px;
				line-height: 66px;
				font-size: 14px;
				color:#555555;
			}
			.buttons{
				float: right;
				height: 100%;
				line-height: 66px;
				button{
					display: inline-block;
					width:31px;
					height:31px;
					margin-right: 9px;
					border:none;
					outline: none;
				}
				input[type='text']{
					display: inline-block;
					width:31px;
					height:31px;
					margin-right: 9px;
					border:none;
					outline: none;
					background: #fff;
					color:#333;
					text-align: center;
				}
				span{
					display: inline-block;
					margin-right: 10px;	
					height: 66px;
					line-height: 66px;
					font-size: 14px;
					color:#555555;
				}
				.button1{
					background: #fff;
					color:#333;
				}
				.button2{
					background-color: #6da583;
					color:#fff;
					font-size: 12px;
				}
			}
		}
		.bigCover{
			position: absolute;
			width:100%;
			height: 100%;
			background-color: rgba(255,255,255,0.6);
			z-index: 6;
		}
	}
</style>


