<template>
  <div id="app">
    <div class="view-wrapper-box">
      <div class="view-wrapper">
        <router-view></router-view>
        <div class="maintitle">欢迎使用广外大节能中心后台管理系统</div>
      </div>
    </div>
    <div class="leftnavbar-wrapper">
      <v-leftnavbar></v-leftnavbar>
    </div>
  </div>
</template>

<script>
  import vLeftnavbar from 'components/v-leftnavbar/v-leftnavbar.vue';


  export default {
    name: 'app',
    components:{
      vLeftnavbar:vLeftnavbar
    },
    created:function(){
    }
  }
</script>

<style scoped lang='less'>

  #app {
    width:100%;
    height: 100%;
    overflow: auto;
    .view-wrapper-box{
      float: left;
      width:100%;
      height: 100%;
      .view-wrapper{
        height: 100%;
        background-color: #f7f7f7;
        position: relative;
        .maintitle{
          position: absolute;
          top:0px;
          left: 0px;
          width: 100%;
          height: 50px;
          line-height: 50px;
          text-align: center;
          font-size: 16px;
          font-weight: 500;
          background-color: #FFFFFF;
          color: #6DA683
        }
      }
    }
    .leftnavbar-wrapper{
      float: left;
      height: 100%;
      margin-left: -100%;
      background-color: #2c3642;      
    }
  }
  @media screen and (max-width:1077px){
    .view-wrapper{
      margin-left: 80px;
    }
    .leftnavbar-wrapper{
      width:80px;
    }
  }
  @media screen and (min-width:1080px){
    .view-wrapper{
      margin-left: 200px;
    }
    .leftnavbar-wrapper{
      width:200px;
    }
  }
</style>



